import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PersonModuleComponent } from './person-module/person-module.component';
import { CourseModuleComponent } from './course-module/course-module.component';
import { ModulePaymentComponent } from './module-payment/module-payment.component';
import { ModulePersonCourseComponent } from './module-person-course/module-person-course.component';
import { ModulePersonDetailComponent } from './module-person-detail/module-person-detail.component';
import { ModulecourseviewComponent } from './modulecourseview/modulecourseview.component';
import { ModulecoursedetailComponent } from './modulecoursedetail/modulecoursedetail.component';
import {TherapyComponent} from './therapy/therapy.component';
const routes: Routes = [
  { path: 'personModule', component: PersonModuleComponent , // this is the component with the <router-outlet> in the template
    children: [
      { 
        path: 'person', // child route path
        component: ModulePersonDetailComponent, // child route component that the router renders
      
      },
      {
        path: 'courses', // child route path
        component: ModulePersonCourseComponent, // child route component that the router renders
      },
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'person'
      }
    ]
  },
  { path: 'courseModule', component: ModulecourseviewComponent,
    children: [
      { 
        path: 'coursedetail', // child route path
        component: ModulecoursedetailComponent, // child route component that the router renders
      
      },
      {
        path: 'view', // child route path
        component: CourseModuleComponent, // child route component that the router renders
      },
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'view'
      }
    ]
  },
  { path: 'therapyModule', component: TherapyComponent },
  { path: 'paymentModule', component: ModulePaymentComponent },
  { path: '',   redirectTo: 'personModule', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }