import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-module-person-detail',
  templateUrl: './module-person-detail.component.html',
  styleUrls: ['./module-person-detail.component.css']
})
export class ModulePersonDetailComponent implements OnInit {
  public items = [];
  public employee = [];
  public selectEmployee;
  public iscreate;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
   this.getItems()
   this.getEmployee()
   this.selectEmployee = {rut:null,name:"",lastname:"",speciality:""};
  }
  getItems(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPerson').subscribe(data => {
      this.items = data;
    });
  }
  getEmployee(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.employee = data;
    });
  }
  getselectemployee(emp){
    this.selectEmployee = {rut:null,name:"",lastname:"",speciality:""};
    if(emp){
      this.selectEmployee =  emp
    }
    this.iscreate =emp
    
  }
  setEmployee(){
   
    if(this.iscreate){
      this.http.post<any>('https://luzvioleta.herokuapp.com/updateEmploye',this.selectEmployee).subscribe(data => {
        this.getEmployee()
      });
    }else{
      this.http.post<any>('https://luzvioleta.herokuapp.com/insertEmployee',this.selectEmployee).subscribe(data => {
        this.getEmployee()
      });
    }
      
  }
}
