import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-therapy',
  templateUrl: './therapy.component.html',
  styleUrls: ['./therapy.component.css']
})
export class TherapyComponent implements OnInit {
  public therapy;
  public relator;
  public person;
  public persontherapy;
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.inittherapy()
    this.getTherapy()
    this.getRelator()
    this.getPerson()
  }
  inittherapy(){
    this.persontherapy = {idperson:"",idtherapist:"",date:"",hour:""}
  }
  getPerson(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectPerson').subscribe(data => {
      this.person = data;
    });
  }
  getTherapy(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selecttherapy').subscribe(data => {
      this.therapy = data;
    });
  }
  getRelator(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.relator = data;
    });
  }
  setTherapy(){
    this.http.post<any>('https://luzvioleta.herokuapp.com/inserttherapy',this.persontherapy).subscribe(data => {
      this.inittherapy()
      this.getTherapy()
    });
  }
}
