import { Component, OnInit } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-modulecoursedetail',
  templateUrl: './modulecoursedetail.component.html',
  styleUrls: ['./modulecoursedetail.component.css']
})
export class ModulecoursedetailComponent implements OnInit {
  public itemhead;
  public items;
  public changeitems;
  public relator;
  constructor( private route: ActivatedRoute,private router: Router,private http: HttpClient) { }

  ngOnInit(): void {
    this.getHead()
    this.getItems()
    this.initchangeitems()
    this.getRelator()
  }
  initchangeitems(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('ID');
    this.changeitems = {ID: null,name:firstParam, enddate: "", initdate: "", inscription: 0, media: "", monthly: 0, relator: null, status: 1}
  }
  getHead(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('ID');
    console.log(firstParam)
    this.itemhead  = {name:'' , description:'', type:'curso'}
    if(firstParam){
      this.http.post<any>('https://luzvioleta.herokuapp.com/selectCoursedetail',{ID:firstParam}).subscribe(data => {
        this.itemhead = data[0];
      });
    }
    
  }
  getItems(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('ID');
    console.log(firstParam)
    if(firstParam){
      this.http.post<any>('https://luzvioleta.herokuapp.com/detallecourse',{ID:firstParam}).subscribe(data => {
        this.items = data;
      });
    }
    
  }
  getRelator(){
    this.http.get<any>('https://luzvioleta.herokuapp.com/selectEmployee').subscribe(data => {
      this.relator = data;
    });
  }
  openModal(item){
    this.changeitems = item
  }
  saveModal(){
    if(this.changeitems.ID){
      this.http.post<any>('https://luzvioleta.herokuapp.com/updateperiod',this.changeitems).subscribe(data => {
        this.getItems()
      });
    }else{
      this.http.post<any>('https://luzvioleta.herokuapp.com/insertperiod',this.changeitems).subscribe(data => {
        this.getItems()
      });
    }
    this.initchangeitems()
  }
  closeModal(){
    this.initchangeitems()
  }
  changeItem(){
    const firstParam: string = this.route.snapshot.queryParamMap.get('ID');
    console.log(firstParam)
    if(firstParam){
      this.http.post<any>('https://luzvioleta.herokuapp.com/updatecourseDescription',this.itemhead).subscribe(data => {
      });
    }else{
      this.http.post<any>('https://luzvioleta.herokuapp.com/insertcourseDescription',this.itemhead).subscribe(data => {
        console.log(data)
        if(data){
          this.router.navigate(['courseModule/coursedetail'], { queryParams: { ID: data[0].ID} });
        }
      });
    }
  }
}
